import React, { useState } from 'react';

import { Link } from "react-router-dom";
import { CardMedia, Chip, makeStyles } from '@material-ui/core';
import _ from 'lodash'

import { CalendarIcon } from '../components/icons'
import data from '../data/data'

export default function LastPost() {
  const [category, setCategory] = useState(null);
  //const classes = useStyles();
  function filterCategory(category) {
    setCategory(category);
  }
  return (
    <>
      <div style={{ padding: 10, margin: 10 }}>
        {
          _.unionBy(data, 'category').map((item, i) =>
            <Chip style={chip} label={item.category} key={i} onClick={() => filterCategory(item.category)} />
          )}
      </div>
      {
        data
          .filter(item => item.category === category || category === null)
          .map(item => (
            <div key={item.seoUrl} style={root}>
              <Link style={{ textDecoration: 'none' }} to={`/blog/${item.seoUrl}`}>
                {/* <CardMedia style={image}
                  component="img"
                  alt={`${item.seoUrl}`}
                  image={require(`../data/images/${item.seoUrl}.png`)}
                /> */}
                <h1 style={title}>{item.title}</h1>
                <span><CalendarIcon style={calendarIcon} /></span>
                <span style={date}>{item.date}</span>
              </Link>
            </div>
           
          )
          )}
    </>
  );
}

 const root = {
    marginBottom: 30,
    borderRadius: 8,
    border: '1px solid #dadce0',
    '&:hover': {
      boxShadow: "1px 1px 1px 1px rgba(60,64,67,0.3)",
    },

  }
 const date = {
    color: '#6d6d6d',
    paddingLeft: 10,
    fontWeight: '500',
    verticalAlign: '12%'
  }
 const calendarIcon = {
    width: 18,
    height: 19,
    paddingLeft: 20,
  }
 const chip = {
    marginLeft: 10,
    marginBottom: 10,
    color: 'black',
    backgroundColor: '#D5D4D2',
    '&:focus': {
      color: "white",
      backgroundColor: '#40AEF8',
    }
  }
 const image = {
    width: '100%',
    borderTopLeftRadius: 8,
    borderTopRightRadius: 8
  }
  const title = {
    paddingLeft: 20,
    paddingTop: 10,
    color: '#3d3d3d',
    fontWeight: '600',
    fontSize: 21,
  }


